<template>
    <div class="search-header">
        <div class="btn-container">
            <el-button v-for="item in showBtnList"
                       :key="item.key"
                       :size="btnSize"
                       @click="operationClick(item)"
                       :icon="item.icon||''"
                       :type='item.type'>{{item.label}}
            </el-button>
            <transition name="el-fade-in-linear">
                <el-button :size="btnSize"
                           v-show="showFiltrate&&revertBtnShow"
                           @click="revert"
                           type='danger'>
                </el-button>
            </transition>
            <el-dropdown style="margin-left: 10px"
                         @command="handleCommand"
                         v-if="hiddenBtnList.length>0">
                <el-button :size="btnSize">
                    <i class="el-icon-arrow-down el-icon--right"/>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item v-for="item in hiddenBtnList"
                                      :command="item"
                                      :key="item.label">{{item.label}}
                    </el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
        <transition name="el-zoom-in-top">
            <el-form v-show="showFiltrate"
                     ref="moreSearch"
                     class="form-container"
                     :model="searchData"
                     label-width="120px"
                     label-position="right"
                     :rules="validRules">
                <template v-for="item in defaultShowList">
                    <el-form-item
                            :label="item.label"
                            v-if="item.type!=='cascade'"
                            :prop="item.key"
                            :key="item.key">
                        <input-content
                                :config="item"
                                :value.sync="searchData[item.key]">
                        </input-content>
                    </el-form-item>
                    <template v-for="ele in cascadeList" v-else>
                        <el-form-item
                                :label="ele.label"
                                :prop="ele.key"
                                :key="ele.key">
                            <el-select v-model="searchData[ele.key]"
                                       :placeholder='`${ele.label}`'
                                       value-key="value"
                                       style="width: 100%"
                                       @change="cascadeChange(ele)"
                                       clearable>
                                <el-option v-for="item in cascadeSelectList[ele.key]" :key="item.value"
                                           :value="{label:item.label,value:item.value}"
                                           :label="item.label">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </template>
                </template>
            </el-form>
        </transition>
        <div class="filter-container">
            <div @click="showFiltrate=!showFiltrate">
                <span :style="{'color':showFiltrate?'#2680D1':''}"></span>
                <img :src="showFiltrate?onFilter:offFilter" alt="">
            </div>
            <div @click="filterDialogShow=true">
                <span></span>
                <img src="./icon/add.png" alt="">
            </div>
        </div>
        <filter-dialog
                :name="name"
                @checkedInput="checkedInput"
                :input-list="propInputList"
                :vis.sync="filterDialogShow">
        </filter-dialog>
    </div>
</template>

<script>
    import inputContent from './inputContent'
    import filterDialog from "./filterDialog";

    export default {
        name: "searchHeader",
        components: {inputContent, filterDialog},
        data() {
            return {
                onFilter: require('./icon/onFilter.png'),
                offFilter: require('./icon/offFilter.png'),
                filterDialogShow: false,
                searchData: {},
                showBtnList: [],
                hiddenBtnList: [],
                showFiltrate: false,
                revertBtnShow: false,
                cascadeList: [],
                cascadeSelectList: {},
                defaultShowList: [],
                propInputList: [],
            }
        },
        props: {
            btnSize: {
                type: String,
                default: 'mini'
            },
            name: {
                type: String,
                required: true
            },
            validRules: {
                type: Object,
                default: () => {
                    return {}
                }
            },
            btnList: {
                type: Array,
                default: () => {
                    return []
                }
            },
            inputList: {
                type: Array,
                default: () => {
                    return []
                }
            },
        },
        watch: {
            'defaultShowList.length': {
                handler(newValue) {
                    this.revertBtnShow = newValue !== 0
                }
            },
            filterDialogShow() {
                if (!this.filterDialogShow) {
                    this.dialogInputInit();
                }
            }
        },
        created() {
            this.dataInit();
        },
        methods: {
            dataInit() {
                this.btnList.forEach(item => {
                    (item.default && item.show && item.show())&&this.showBtnList.push(item);
                    (!item.default&& item.show && item.show()) && (this.hiddenBtnList.push(item))
                });
                this.dialogInputInit();
                this.cascadeInit();
            },
            dialogInputInit() {
                this.propInputList = this.inputList;
                this.defaultShowList = [];
                let list = JSON.parse(localStorage.getItem(`searchList_${this.name}`));
                if (list) {
                    this.propInputList.forEach(e => {
                        //false true
                        //listdefault
                        e.default = false;
                    });
                    //list  
                    list.forEach(item => {
                        this.propInputList.forEach(e => {
                            if (item === e.label) {
                                e.default = true;
                                this.defaultShowList.push(e);
                            }
                        });
                    });
                } else {
                    this.defaultShowList = this.inputList.filter(item => {
                        return item.default
                    });
                }
            },
            cascadeInit() {
                let vm = this;
                this.defaultShowList.forEach(item => {
                    if (item.type === 'cascade') {
                        vm.cascadeList = [];
                        Object.values(item.cascadeList).forEach((e, index) => {
                            vm.cascadeSelectList[e.key] = [];
                            vm.cascadeList.push(e);
                            if (index === 0) {
                                e.list().then(res => {
                                    vm.cascadeSelectList[e.key] = res;
                                    vm.$forceUpdate();
                                })
                            }
                        });
                    }
                });
            },
            handleCommand({method, key}) {
                method(key);
            },
            checkedInput(list) {
                this.defaultShowList = [];
                list.forEach(item => {
                    this.inputList.forEach(e => {
                        if (item === e.label) {
                            this.defaultShowList.push(e);
                        }
                    });
                });
                this.cascadeInit();
                this.searchData = {};
                localStorage.setItem(`searchList_${this.name}`, JSON.stringify(list));
            },
            //
            revert() {
                this.searchData = {};
            },
            cascadeChange(ele) {
                // 
                if (!ele.nextKey) {
                    this.$forceUpdate();
                    return;
                }
                //  
                let keys = Object.keys(this.defaultShowList.filter(item => {
                    return item.type === 'cascade'
                })[0].cascadeList);
                let currentIndex = keys.indexOf(ele.key);
                for (let i = currentIndex + 1; i < keys.length; i++) {
                    this.cascadeSelectList[keys[i]] = [];
                    this.searchData[keys[i]] = '';
                }
                let vm = this;
                let data = this.cascadeList.filter(item => {
                    return ele.nextKey === item.key;
                })[0];
                data.list(this.searchData[ele.key].value).then(res => {
                    vm.$set(vm.cascadeSelectList, data.key, res);
                    vm.$forceUpdate();
                })
            },
            //
            operationClick(data) {
                //
                if (data.isSearch) {
                    let params = window.JSON.parse(window.JSON.stringify(this.searchData));
                    //
                    let cascadeList = this.defaultShowList.filter(item => {
                        return item.type === 'cascade'
                    });
                    //
                    if (cascadeList.length > 0) {
                        cascadeList.forEach(item => {
                            //value
                            let queryKey = item.queryKey || 'value';
                            let keys = Object.keys(item.cascadeList);
                            keys.forEach(el => {
                                if (params[el]) {
                                    params[el] = params[el][queryKey];
                                }
                            })
                        });
                    }
                    //
                    let timeRangeList = this.defaultShowList.filter(item => {
                        return item.type === 'dateRange'
                    });
                    //
                    if (timeRangeList.length > 0) {
                        timeRangeList.forEach(item => {
                            params = {...params, ...params[item.key]};
                            delete params[item.key];
                        })
                    }
                    data.method( data.key,this.clearObject(params));
                } else {
                    data.method(data.key);
                }
            },
            clearObject(obj) {
                for (const key in obj) {
                    if (typeof obj[key] != 'number' && !obj[key]) {
                        delete obj[key]
                    }
                }
                return obj
            }
        }
    }
</script>

<style scoped lang='scss'>
    .search-header {
        .form-container {
            display: flex;
            flex-wrap: wrap;
            margin: 15px 0;

            /deep/ .el-form-item {
                width: 30%;
                margin: 5px 3% 15px 0;

                .el-form-item__label {
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
            }
        }

        .btn-container {
            text-align: right;
        }

        .filter-container {
            font-size: 14px;
            display: flex;

            div {
                cursor: pointer;
                display: flex;
                align-items: center;
                margin-right: 20px;

                span {
                    margin-right: 5px;
                }
            }

        }
    }
</style>
